@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  border: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none !important;
  color: inherit !important;
}

.img-slide {
  max-width: 700px;
}

button {
  border: none;
}

.cardProduct {
  flex-direction: column;
  display: flex;
  align-items: center;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.318);
  width: 327px;
  margin: 20px 30px;
  border-radius: 5px;
  padding: 10px 10px;
}
.cardProduct .main__iTop {
  position: relative;
  justify-content: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.cardProduct .main__iTop .main__categori {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 157px;
  height: 23px;
  background: #259b24;
  border-radius: 5px;
}
.cardProduct .main__iTop .main__categori span {
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 125.4%;
  /* identical to box height, or 18px */
  text-align: center;
  color: #ffffff;
}
.cardProduct .main__iTop .heart {
  cursor: pointer;
  position: absolute;
  right: 10px;
}
.cardProduct .main__iTop .star-box {
  width: 53px;
  height: 32px;
  left: -10px;
  top: 0;
  background-color: #ff9800;
  border-radius: 0px 5px 5px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
}
.cardProduct .main__iCenter {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px 0;
}
.cardProduct .main__iCenter .main__iInfo {
  position: absolute;
  left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cardProduct .main__iCenter .main__iInfo .main__iL {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cardProduct .main__iCenter .main__iInfo .main__iL span {
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 125.4%;
  /* or 25px */
  color: #673ab7;
}
.cardProduct .main__iCenter .main__iInfo .main__iL p {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 125.4%;
  /* or 15px */
  color: #151621;
}
.cardProduct .main__iDownCenter {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 0;
}
.cardProduct .main__iDownCenter span {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 125.4%;
  /* or 23px */
  color: #151621;
}
.cardProduct .main__iBottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.cardProduct .main__iBottom .main__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 0;
}
.cardProduct .main__iBottom .main__top p {
  margin: none;
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 125.4%;
  /* identical to box height, or 15px */
